.resume {
    font-family: Apple SD Gothic Neo;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 100%;
}

.title {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
}

.title-image {
  float: left;
}

.contact {
  /* width:  510px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 30px;
}

.contact-title {
  display: flex;
  align-self: baseline;
}

.icon-container {
  display:flex;
  /* flex-direction: column; */
  align-items: baseline;
}

.icon-wrapper {
  margin: 10px; /* 원하는 간격으로 조정 */
}

.profile-image {
  width: 260px;
  height: 260px;
  border-radius: 50%;
  object-fit: cover;
  margin-top: 40px;
  margin-bottom: 40px;
  margin-right: 30px;
  align-items: end; 
}

.title-text {
  float: right;
  width: 520px;
  display: flex;
  flex-direction: column;
  justify-content : flex-start;
  margin-top: 40px;
  margin-bottom: 40px;
}

.typing-effect {
  text-align: left;
  display: inline-block;
  justify-content: left;
  white-space: nowrap; /* 텍스트가 한 줄로 표시되도록 설정 */
  overflow: hidden; /* 넘치는 텍스트는 숨기도록 설정 */
}

.introduce {
  width: 100%;
  border-top: 1px solid;
  /* border-bottom: 1px solid; */
  display: flex;
  flex-direction: column;
  max-width: 810px; /* 최대 너비 설정 */
  /* margin: 0 auto; 가운데 정렬을 위해 margin을 auto로 설정 */
  font-family: Apple SD Gothic Neo;
  white-space: pre-wrap;
}

.introduce p {
  max-width: 810px;
  line-height: 1.55; /* 글자 간격 설정 */
}

.skill {
  width: 100%;
  max-width: 810px;
  justify-content: center;
  text-align: center;
  display: flex;
  flex-direction: column;
  border-top: 1px solid;
  /* border-bottom: 1px solid; */
}

.skill-list {
  position: relative;
  text-align: left;
  left: 240px;
  max-width: 380px;
}

.project {
  margin-top: 7px;
  padding-top: 20px;
  padding-left: 20px;
  border-radius: 5px;
  border: 1px solid;
  margin-left: auto;
  margin-right: auto;
  word-wrap: break-word; /* 글자가 넘칠 때 다음 줄로 넘어가도록 설정 */
  box-sizing: border-box;
}

.project h4 {
  margin-left: 5px;
  text-align: left;
}

.contribution-list {
  justify-content: center;
  text-align: left;
  padding-left: 20px; /* 리스트 간격을 조절할 수 있는 값 */
  margin-top: 0%; /* 위쪽 간격 조정 */
  margin-bottom: 0%; /* 아래쪽 간격 조정 */
}
.contribution-list li {
  width: 100%;
  list-style-type: disc; /* 리스트 앞에 점을 추가 */
  line-height: 0; /* 글자 간격 조절 */
}

.experience {
  width: 100%;
  max-width: 810px;
  border-top: 1px solid;
}

.experience p {
  line-height: 0.5;
}

.education {
  border-top: 1px solid;
  width: 100%;
  max-width: 810px;
}

.license-border {
  border-bottom: 1px solid;
  max-width: 810px;
  width: 100%;
}

.license {
  /* border-top: 1px solid; */
  /* width: 100%; */
  max-width: 810px;
  text-align: center;
}

.license ul {
  text-align: center;
  list-style-type: disc; 
  line-height: 0;
}

.license li {
  justify-content: center;
  text-align: left;
}

.footers {
  border: 0.5px solid #ccc;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding: 3px 10px; /* 간격을 조절하는 부분 */
}

.footers div {
  margin-left: 10px;
  margin-right: 10px;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

h1 {
  margin-bottom: 10px;
}

h3 {
  margin-bottom: 5px;
}

ul {
  float: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

li {
  margin-bottom: 30px;
}

.dark-mode {
  background-color: #222831;
  color: #ececec;
}

.dark-mode .toggle-dark-mode {
  color: #4ecca3;
}

.toggle-dark-mode {
  position: absolute;
  top: 1%;
  right: 1%;
  display: inline-block;
  font-size: 16px;
  padding: 8px 16px;
  border-radius: 4px;
  border: 1px solid #ccc;
  background-color: transparent;
  outline: none;
  cursor: pointer;
  margin: 10px;
}

.toggle-dark-mode:hover {
  background-color: #ececec;
  color: #222831;
}
  
/* 기존 CSS 스타일을 유지하고 미디어 쿼리를 추가합니다. */

/* 태블릿 이하 크기에서 적용할 스타일 */
@media only screen and (max-width: 768px) {

  .toggle-dark-mode {
    font-size: 12px; /* 글자 크기를 줄입니다. */
    padding: 5px 9px; /* 내부 패딩을 줄입니다. */
    right: 0.5%; /* 오른쪽에서의 위치를 0으로 설정하여 오른쪽 끝에 붙입니다. */
    top: 1%; /* 상단 위치를 조정할 수 있습니다. */
    position: absolute;
    display: inline-block;
    border-radius: 4px;
    border: 1px solid #ccc;
    background-color: transparent;
    outline: none;
    cursor: pointer;
    margin: 10px;
  }

  .resume {
      align-items: center; /* 가운데 정렬 */
      padding: 10px; /* 패딩 조정 */
  }

  .title {
      flex-direction: column; /* 세로로 쌓기 */
      text-align: center;
  }

  .title-image,
  .title-text {
      justify-content: center;
      float: none;
      margin-right: 0;
      font-size: 9px; 
  }

  .contact {
      align-items: center; /* 가운데 정렬 */
      width: 100%; /* 태블릿 크기에서는 박스의 너비를 100%로 조정하여 가득 차도록 함 */
  }

  .contact-title,
  .icon-container,
  .icon-wrapper {
      justify-content: center;
      display: flex;
      flex-direction: column; /* 아이콘을 수직으로 쌓기 위해 변경 */
      align-items:flex-start; /* 센터 정렬 */
  }

  .profile-image {
      margin-right: 0; /* 오른쪽 마진 제거 */
      align-self: center; /* 가운데 정렬 */
  }

  .project {
    padding: 20px; /* 상하좌우 패딩을 동일하게 설정, 필요하다면 조정 */
    width: auto; /* 필요에 따라 고정된 너비를 auto로 변경하여 너비 조정 */
    /* 필요한 경우 추가적인 스타일 변경을 여기에 추가 */
  }

  .contribution-list {
    text-align: left;
    padding-left: 20px; /* 리스트 간격을 조절할 수 있는 값 */
    margin-top: 0; /* 위쪽 간격 조정 */
    margin-bottom: 0; /* 아래쪽 간격 조정 */
    display: flex; /* Flex container로 만듭니다. */
    flex-direction: column; /* 항목을 세로로 나열합니다. */
  }
  
  .contribution-list li {
    width: 100%;
    list-style-type: disc; /* 리스트 앞에 점을 추가 */
    line-height: 1.2; /* 적절한 글자 간격을 설정합니다. */
    word-wrap: break-word; /* 글자가 넘칠 때 줄바꿈이 되도록 설정합니다. */
  }

  .typing-effect,
  .introduce,
  .introduce p,
  .skill,
  .skill-list,
  .project,
  .project h4,
  .contribution-list,
  .contribution-list li,
  .experience,
  .experience p,
  .education,
  .license-border,
  .license,
  .license ul,
  .license li,
  .footers {
      text-align: center;
      float: none; /* float 제거 */
      align-items: center; /* 가운데 정렬 */
      justify-content: center;
      margin-left: auto;
      margin-right: auto;
  }

  .skill-list {
      left: 0; /* 왼쪽 정렬 제거 */
  }
}
